/* Common styles for all components */
.component {
    margin: 20px;
    padding: 20px;
    border: 2px solid #007bff;
    border-radius: 8px;
    background-color: #f0f8ff;
  }
  
  /* Styles specific to the component with JSX */
  .with-jsx header,
  .with-jsx section,
  .with-jsx aside,
  .with-jsx footer {
    margin: 10px 0;
  }
  
  /* Styles specific to function component and class component tables */
  .function-component table,
  .class-component table {
    width: 100%;
    border-collapse: collapse;
  }
  
  /* Styles for table header and table data cells */
  .function-component th,
  .function-component td,
  .class-component th,
  .class-component td {
    border: 1px solid #007bff;
    padding: 8px;
    text-align: left;
  }
  